import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { ToastController } from 'ionic-angular';

import { AboutPage } from '../about/about';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage
{

  constructor(public navCtrl: NavController, public toastCtrl: ToastController)
  {
      
  }

  goToAboutPage()
  {
    this.navCtrl.setRoot( AboutPage );
  }

  goToHomePage()
  {
    this.navCtrl.setRoot( HomePage );
  }

  presentToast()
  {
    const toast = this.toastCtrl.create({
      message: 'You have found Kathy!',
      position: 'middle',
      duration: 2000,
      cssClass: 'toast-box-big',
    });

    toast.present();
  }

  presentToast_Buildbot()
  {
    const toast = this.toastCtrl.create({
      message: 'Robot Buildie',
      position: 'middle',
      duration: 1500,
      cssClass: 'toast-box',
    });

    toast.present();
  }

  presentToast_Panda()
  {
    const toast = this.toastCtrl.create({
      message: 'Panda Buildie',
      position: 'middle',
      duration: 1500,
      cssClass: 'toast-box',
    });

    toast.present();
  }

  presentToast_Puppy()
  {
    const toast = this.toastCtrl.create({
      message: 'Puppy Buildie',
      position: 'middle',
      duration: 1500,
      cssClass: 'toast-box',
    });

    toast.present();
  }

  presentToast_Kitty()
  {
    const toast = this.toastCtrl.create({
      message: 'Kitty Buildie',
      position: 'middle',
      duration: 1500,
      cssClass: 'toast-box',
    });

    toast.present();
  }

  presentToast_Pig()
  {
    const toast = this.toastCtrl.create({
      message: 'Piggie Buildie',
      position: 'middle',
      duration: 1500,
      cssClass: 'toast-box',
    });

    toast.present();
  }

  presentToast_Duck()
  {
    const toast = this.toastCtrl.create({
      message: 'Duckie Buildie',
      position: 'middle',
      duration: 1500,
      cssClass: 'toast-box',
    });

    toast.present();
  }

  presentToast_Emoji()
  {
    const toast = this.toastCtrl.create({
      message: 'Emoji Buildie',
      position: 'middle',
      duration: 1500,
      cssClass: 'toast-box',
    });

    toast.present();
  }

}



